import 'whatwg-fetch';
import '../styles/tachyons.scss';

import React, { Component } from 'react';
import { Helmet } from 'react-helmet';
import { getUV } from '../utils/api';
import ReactGA from 'react-ga';

import Wrapper from './Wrapper/index';
import Header from './Header';
import WipeOverlay from './WipeOverlay/index';
import Footer from './Footer/index';

import Main from './Main/index';
import MainHigh from './MainHigh/index';

class App extends Component {
	state = {
		uv: false
	};

	componentDidMount() {
		ReactGA.initialize('UA-140592709-1');
		ReactGA.pageview(window.location.pathname + window.location.search);

		getUV().then((response) => {
			this.setState({
				uv: response.uv
			});
		});
	}

	render() {
		return (
			<Wrapper>
				<Helmet>
					<title>FAC50</title>
					<script
						type="text/javascript"
						src="//fast.fonts.net/jsapi/489d04e9-0c75-45d6-8a27-023e6605649c.js"
					/>
				</Helmet>
				<Header uvIndex={this.state.uv} />
				{this.state.uv >= 3 ? <MainHigh /> : <Main />}
				<WipeOverlay />
				<Footer />
			</Wrapper>
		);
	}
}

export default App;
