import React, { Component } from 'react';

import './style.scss';

import bottleImg from '../../images/fac-50-bottle.png';
import useProtection from '../../images/fac50-use-protection.png';

const images = [ bottleImg, useProtection ];

class Main extends Component {
	constructor(props) {
		super(props);
		this.state = {
			image: false
		};
	}
	componentDidMount() {
		this.setState({
			image: this.loadImg()
		});
	}

	loadImg() {
		return images[Math.floor(Math.random() * images.length)];
	}

	render() {
		return (
			<main className="main-container mt4 mt0-l w-90 center flex flex-column flex-row-ns items-center justify-center pa3-ns pv4-ns">
				<div className="w-100 w-third-ns mb3 dn db-ns">
					<h2 className="fac-bold ma0 f5 f3-ns">
						MANCHESTER, IT’S HOTTING UP AND SUMMER IS ALMOST HERE. IT’S NEARLY FESTIVAL SEASON. LET’S PARTY
						IN THE SUN PROTECTED.
					</h2>
				</div>
				<div className="w-100 w-third-ns mb3 mh4-l flex flex-column items-center">
					<img
						className={`w-100 mw4 main-image ${this.state.image.toString().includes('bottle')
							? 'bottle'
							: 'face'}`}
						src={this.state.image}
						alt="FAC50 bottle"
					/>
				</div>
				<div className="w-100 w-third-ns mb3 dn db-ns">
					<h2 className="fac-regular f6 f5-ns">
						Living our best lives in the sun shouldn’t mean we don’t take care of ourselves. According to
						research over 86% of Melanoma Skin Cancer cases are preventable.
						<span className="db pt3">
							Applying suncream every 2 hours can protect our skin from a lifetime of skin damage.
						</span>
					</h2>
				</div>
			</main>
		);
	}
}

export default Main;
